<template>
    <div class="answercontenttest">
        <!--  -->
        <answeralert :answertrue="answertorf.true" @starttime="starttimefn" @pushtest="pushtestfn" v-if="alertvalue"
            :answerno="answerno" :hsMessage="titleMessage.hsMessage">
        </answeralert>
<div class="answercontenttest-title">
        <Answertitle :hsMessagevalue="titleMessage.hsMessagevalue" :hsMessage="titleMessage.hsMessage"
            :spanMessage="titleMessage.spanMessage"></Answertitle>
       </div>
        <div class="content">
            <div class="test-title">
                <div class="test-icon">
                    <span v-if="answerLength.length===4"></span>
                    <span v-else></span>
                </div>
                <span class="test-question">
                    {{nowdate.question}}
                </span>
            </div>
            <div v-if="nowdate.url" class="test-img">
                <img :src="nowdate.url">
            </div>
            <div class="answerb">
                <li v-for="(item, index) in answerLength" :key="index">
                    <div class="option">
                        <span @click="checkanswerfn(index+1)" class="option-item">{{indexAz[index+1]}}</span>
                        <span class="chose-question">{{item}}</span>
                    </div>
                </li>
            </div>
        </div>
        <Answerfooter @stoptime="stoptimefn" @pushtest="pushtestfn" :answerno="answerno" :answertrue="answertorf.true"
            :answerfalse="answertorf.false" :answerNum="answerNum">
        </Answerfooter>
    </div>

</template>

<style lang="less">
    .answercontenttest {
        margin-bottom: 5rem;
        display: flex;
        flex-wrap: wrap;
.answercontenttest-title{
    width: 100%;
    background:white;
}
        .content {
            padding: 2rem;
            width: 100%;

            .test-title {

                font-size: 1.8rem;

                .test-icon {
                    display: inline-block;
                    font-size: 1.2rem;
                    background: yellowgreen;
                    border-top-left-radius: 20rem;
                    border-bottom-left-radius: 20rem;
                    border-top-right-radius: 20rem;
                    padding: 0.5rem 1rem;
                    margin-right: 1rem;

                    span {
                        color: white;
                    }
                }
            }

            .test-img {
                width: 100%;
                display: flex;
                justify-content: center;
                margin: 2rem 0;

                img {
                    max-width: 100%;
                }
            }

            .answerb {
                display: flex;
                flex-wrap: wrap;
                margin-top: 3rem;

                li {
                    width: 100%;
                    margin: 1rem 0;

                    .option {
                        font-size: 2rem;

                        .option-item {
                            padding: 0.5rem;
                            border-radius: 50%;
                            box-shadow: 0 0 0.5rem 0.2rem lightgray;
                            cursor: pointer;
                        }

                        .chose-question {
                            line-height: 2;
                            margin-left: 2rem;
                        }
                    }
                }
            }

        }
    }
</style>

<script>
    // st1 
    var st1;
    import Answertitle from '@/views/Answercompent/Answertitle';
    import Answerfooter from '@/views/Answercompent/Answerfooter';
    import Answeralert from '@/views/Answercompent/Answeralert';
    import {
        constants
    } from 'crypto';


    export default {

        components: {
            Answertitle,
            Answerfooter,
            Answeralert
        },
        props: ['testdata'],
        name: 'answercontenttest',
        data() {
            return {
                alertvalue: false,
                titleMessage: {
                    hsMessage: 2700,
                    spanMessage: '',
                    hsMessagevalue: '/answer'
                },
                dataGroups: this.testdata,
                createidgroups: [],
                answer: null,
                num: 0,
                nowdate: null,
                indexAz: {
                    1: 'A',
                    2: 'B',
                    3: 'C',
                    4: 'D'
                },
                //
                answerLength: [],

                //
                answerNum: 100,

                answerno: 0,
                answertorf: {
                    true: 0,
                    false: 0
                }
            }
        },
        beforeRouteLeave(to, from, next) {

            to.meta.answertrue = this.answertorf.true
            console.log(to.meta.answertrue)
            next()
        },
        methods: {
            stoptimefn() {
                clearInterval(st1)
            },
            starttimefn() {
                st1 = setInterval(() => {
                    this.titleMessage.hsMessage = this.titleMessage.hsMessage - 1;
                    if (this.titleMessage.hsMessage === 0) {
                        clearInterval(st1)
                        alert('')

                    }


                }, 1000);

            },
            pushtestfn(booleanvalue) {
                this.alertvalue = booleanvalue;
            },
            checkanswerfn(x) {
                this.answerLength.length = 0
                if (x === Number(this.answer)) {
                    this.answertorf.true += 1
                    console.log('')
                } else {
                    this.answertorf.false += 1
                    console.log('')

                    //
                    let locationinfo = window.localStorage.ErrorBook
                    //
                    let newLocationinfo = []
                    for (let index = 0; index < locationinfo.split(',').length; index++) {
                        //
                        if (locationinfo.split(',')[index] === '') {
                            continue;
                        }
                        //
                        if (newLocationinfo.indexOf(locationinfo.split(',')[index]) === -1) {
                            newLocationinfo.push(locationinfo.split(',')[index])
                        }
                    }
                    //ID
                    let errorId = this.createidgroups[this.num]
                    //ID
                    newLocationinfo.push(errorId)
                    //
                    window.localStorage.ErrorNum = newLocationinfo.length
                    //ID
                    window.localStorage.ErrorBook = newLocationinfo.toString()
                }
                this.num++
                this.answerno++
            },
        },

        watch: {
            num() {
                //
                if (this.num === this.createidgroups.length) {
                    this.$router.push('Answerresult')
                    console.log('')
                    clearInterval(st1)
                    return
                }
                //
                this.nowdate = this.dataGroups.result[this.createidgroups[this.num]]
                this.answer = this.dataGroups.result[this.createidgroups[this.num]].answer
                for (let key in this.nowdate) {
                    if (key.match('item')) {
                        this.answerLength.push(this.nowdate[key])
                    }
                }
            }
        },

        created() {
            st1 = setInterval(() => {
                this.titleMessage.hsMessage = this.titleMessage.hsMessage - 1;
                if (this.titleMessage.hsMessage === 0) {
                    alert('')
                    console.log(clearInterval(st1))
                    this.$router.push('Answerresult')
                }

            }, 1000);

            if (this.dataGroups.desc === '') {
                //i
                for (let i = 0; i < this.answerNum;) {
                    let createid = Math.floor(Math.random() * this.dataGroups.result.length)
                    if (this.createidgroups.indexOf(createid) === -1) {
                        this.createidgroups.push(createid)
                        i++
                    }
                }
                //
                this.nowdate = this.dataGroups.result[this.createidgroups[this.num]]
                this.answer = this.dataGroups.result[this.createidgroups[this.num]].answer
                for (let key in this.nowdate) {
                    if (key.match('item')) {
                        this.answerLength.push(this.nowdate[key])
                    }
                }
            } else {
                alert('')
            }
        },
    }
</script>