<template>
    <div class="answeralert">
        <div class="answer-infomation">
            <div class="answer-content">
                <h5>{{answertitle}}</h5>
                <div class="answer-no">
                    <div class="answer-route">
                        <canvas id="canvas" ref="canvas" width="200" height="200"></canvas>
                    </div>
                </div>
                <div class="answer-time">{{mins}}:{{secounds}}</div>
            </div>

            <div class="answer-btn">
                <button class="answer-push" @click="pushtest()"></button>
                <button class="answer-next" @click="pushfn(false),starttimefn()"></button>
            </div>
        </div>

    </div>
</template>
<script>
    export default {
        props: ['hsMessage', 'answerno', 'answertrue'],
        name: 'answeralert',
        data() {
            return {
                answernos: 100 - this.answerno,
                mins: null,
                secounds: null,
                answertitle: null
            }
        },
        methods: {
            pushtest() {
                this.$router.push('Answerresult')
            },
            pushfn(booleanvalue) {
                this.$emit('pushtest', booleanvalue)
            },
            starttimefn() {
                this.$emit('starttime')
            }
        },
        created() {
            console.log(this.answertrue)
            if (this.answertrue === 100) {
                this.answertitle = ''
            } else if (this.answertrue >= 95) {
                this.answertitle = ''
            } else if (this.answertrue >= 90) {
                this.answertitle = ''
            } else if (this.answertrue >= 80) {
                this.answertitle = ''
            } else if (this.answertrue < 60) {
                this.answertitle = ''
            } else {
                alert('Bug')
            }

        },
        watch: {
            //
            'hsMessage'() {
                this.mins = parseInt(this.hsMessage / 60),
                    this.secounds = parseInt(this.hsMessage % 60)
                if (this.secounds < 10) {
                    this.secounds = '0' + this.secounds
                }
                if (this.mins < 10) {
                    this.mins = '0' + this.mins
                }
            }
        },
        mounted() {
            //
            this.mins = parseInt(this.hsMessage / 60),
                this.secounds = parseInt(this.hsMessage % 60)
            if (this.secounds < 10) {
                this.secounds = '0' + this.secounds
            }
            if (this.mins < 10) {
                this.mins = '0' + this.mins
            }
            //
            let canvas = this.$refs.canvas
            let ctx = canvas.getContext('2d')
            let routenum = 2

            function drawfn(x, textx) {
                ctx.clearRect(0, 0, 200, 200);
                ctx.arc(100, 100, 90, 0, 2 * Math.PI);
                ctx.strokeStyle = 'lightgray';
                ctx.lineWidth = 10;
                ctx.stroke()
                ctx.beginPath()
                ctx.arc(100, 100, 90, 0, x * Math.PI);
                ctx.strokeStyle = 'red';
                ctx.lineWidth = 10;
                ctx.font = "1.5rem Arial"
                ctx.fillText('', 100, 80, 200);
                ctx.stroke()
                ctx.beginPath()
                ctx.font = "1.5rem Arial"
                ctx.fillText(textx, 100, 110, 200);
                ctx.textAlign = 'center';
            }

            var st1 = setInterval(() => {
                if (routenum.toFixed(2) == (2 / 100) * this.answerno) {
                    clearInterval(st1)
                    return
                } else {
                    if (this.answerno == 0) {
                        drawfn(0, 100 - this.answerno + '')
                    } else {
                        drawfn(routenum, 100 - this.answerno + '')
                    }
                }
                routenum -= 0.01
            }, 1);

        },
    }
</script>
<style scope lang="less">
    .answeralert {
        z-index: 9999;
        position: fixed;
        display: flex;
        justify-content: center;
        width: 100%;
        align-items: center;
        height: 100%;
        background: rgba(95, 94, 94, 0.363);

        .answer-infomation {

            border-radius: 1rem;
            font-size: 2rem;
            display: flex;
            justify-content: center;
            width: 70%;
            background: white;
            flex-wrap: wrap;
            text-align: center;

            .answer-content {

                h5 {
                    color: rgb(236, 81, 34);
                    flex-basis: 100%;
                    padding: 2rem 0;
                }

                .answer-no {
                    flex-basis: 100%;
                }

                .answer-time {
                    font-size: 1.5rem;
                    flex-basis: 100%;
                    padding: 2rem;
                }
            }

            .answer-btn {
                flex-basis: 100%;
                display: flex;

                button {
                    flex-basis: 100%;
                    background: none;
                    border: none;
                    padding: 1rem;
                    cursor: pointer;

                    &:focus {
                        outline: none;
                    }
                }

                .answer-push {
                    background: lightgray;
                }

                .answer-next {
                    background: rgb(8, 214, 8);
                    color: white;
                }
            }

        }
    }
</style>